/* @flow */

import React from 'react';
import styled from 'styled-components';
import Header from '../../components/Header'
import Navigation from '../../components/Navigation'
import Footer from '../../components/Footer'
import ContentWrapper from '../../components/ContentWrapper';
import {Parallax} from 'react-parallax';
import YouTube from 'react-youtube';

const waterImg = ``;
const buddhaImg = ``;

const Container = styled.div`
`;

const Title = styled.h2`
  color: #362c2a;
  margin-top: 20px;
`;

const Para = styled.p`
  color: #362c2a;
  line-height: 1.4;
  margin: 5px;
`;

const TextWrapper = styled.div`
  max-width: 640px;
  margin: 0 auto;
  overflow: hidden;
`;

const StyledImage = styled.img`
  float: left;
  margin: 20px 20px 10px 0;
  border-radius: 15px;
`;

const StyledParallax = styled(Parallax)`
  height: 200px;
`;

type Props = {};

class ReikiLevel1 extends React.Component<Props> {
  render() {
    return (
      <Container>
        <Navigation/>
        <Header title="Reiki Level 1"/>
        <ContentWrapper>
          <TextWrapper>
            {this.createYouTube()}
          </TextWrapper>
          <Footer/>
        </ContentWrapper>
      </Container>
    );
  }

  createYouTube() {
    const props = {
      videoId: `HZpFm85UYIo`,
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 1,
        playlist: [`jevsbP676qc`, `PAMi5G8Idzs`],
        listType: `playlist`,
      },
      configuration: {
        playlist: `PLOx9-zLf4DPSFAh78hwfDg9ChPPqxIMca`
      }
    };
    return <YouTube {...props}/>
  }
}

export default ReikiLevel1;



